<script lang="ts" setup>
import { ref } from 'vue'

const status = ref('');
const upgradeReady = ref(false);
let upgrade = () => { /* NoOP */ };

const setUpgradeReady = (waitingWorker: ServiceWorker) => {
    status.value = '';
    upgradeReady.value = true;
    upgrade = () => {
        navigator.serviceWorker.addEventListener('controllerchange', () => {
            window.location.reload();
        });
        waitingWorker.postMessage({ type: 'SKIP_WAITING' });
    }
}

if (process.env.NODE_ENV !== 'development' && 'serviceWorker' in navigator) {
    navigator.serviceWorker.ready.then(() => {
        status.value = '';
    });
    navigator.serviceWorker.register(`${process.env.BASE_URL}service-worker.js`)
        .then(registration => {
            if (registration.waiting)
                setUpgradeReady(registration.waiting);
            registration.addEventListener('updatefound', () => {
                if (navigator.serviceWorker.controller)
                    status.value = '';
                else
                    status.value = '';

                registration.installing!.addEventListener('statechange', function() {
                    if (this.state == 'installed' && navigator.serviceWorker.controller) {
                        setUpgradeReady(this);
                    }
                });
            });
        })
        .catch(e => status.value = '' + e.message);
}
</script>

<template>
    <span>{{status}} <a v-if="upgradeReady" href="#" @click.prevent="upgrade()"></a></span>
</template>
