<template>
    <div class="building-params">
        <h3>{{t('')}}</h3>
        <div><label>{{t('')}}</label><span class="v">{{(p.periodTicksCount / 60).toLocaleString([], { minimumFractionDigits: 2, maximumFractionDigits: 2 })}}{{ t('') }}</span></div>
        <div><label>{{t('')}}</label><span class="v">{{(p.targetCargoAmount / 10).toLocaleString([], { minimumFractionDigits: 1, maximumFractionDigits: 1 })}}{{ t('') }}</span></div>
        <div class="r">
            <div class="p" style="margin-inline-end: auto;"><label>{{t('')}}</label><span class="v">{{operator}}</span></div>
            <div class="p"><span class="v"><ColorPreview :color-id="p.passColorId"/></span><label>{{t('')}}</label></div>
            <div class="p"><span class="v"><ColorPreview :color-id="p.failColorId"/></span><label>{{t('')}}</label></div>
        </div>
        <div>
            <label>{{t('')}}</label>
            <span class="v">
                <BuildingIcon v-if="p.cargoFilter > 0" :icon-id="itemIconId(p.cargoFilter)" />
                <span v-else></span>
            </span>
        </div>
        <div>
            <label>{{ t('') }}</label>
            <span class="v">{{ genDesc }}</span>
        </div>
    </div>
    <div class="building-params">
        <h3>{{t('')}}</h3>
        <div><label>{{t('')}}</label><span class="v">{{systemWarningMode}}</span></div>
        <div v-if="p.systemWarningMode > 0">
            <label>{{t('')}}</label>
            <span class="v"><BuildingIcon :icon-id="p.systemWarningIconId" /></span>
        </div>
        <div><label>{{t('')}}</label><span class="v">{{alarmMode}}</span></div>
    </div>
    <div v-if="p.alarmMode > 0" class="building-params">
        <div><label>{{t('')}}</label><span class="v">{{tone}}</span></div>
        <div><label>{{t('')}}</label><span class="v">{{p.falloffRadius[1]}}{{t('')}}</span></div>
        <div><label>{{t('')}}</label><span class="v">{{truth(p.repeat)}}</span></div>
        <div><label>{{t('')}}</label><span class="v">{{pitch}}</span></div>
        <div><label>{{t('')}}</label><span class="v">{{p.volume}}</span></div>
        <div v-if="hasLength"><label>{{t('')}}</label><span class="v">{{p.length.toLocaleString([], { minimumFractionDigits: 1, maximumFractionDigits: 1 })}}{{ t('') }}</span></div>
    </div>
</template>

<script lang="ts">
const allOperators = ['=', '', '', '>', '', '<'];
const allPitchs = ['C', 'C#', 'D', 'D#', 'E', 'F', 'F#', 'G', 'G#', 'A', 'A#', 'B'];
</script>

<script lang="ts" setup>
import { computed } from 'vue';
import { useI18n } from 'vue-i18n';

import { BlueprintBuilding, MonitorParameters, SpawnItemOperator } from '@/blueprint/parser';
import { itemIconId } from '@/data/icons';
import { midiMap } from '@/data/midi';
import { truth } from '@/utils';
import BuildingIcon from './BuildingIcon.vue';
import ColorPreview from './ColorPreview.vue';

const { t } = useI18n();

const allTones = new Map<number, string>([
    [20, t('-1')], [21, t('-2')], [22, t('-3')], [23, t('-4')], [24, t('-5')],
    [1, t('-1')], [2, t('-2')],
    [3, t('-1')], [4, t('-2')],
    [5, t('-1')], [6, t('-2')],
    [7, t('-1')], [8, t('-2')], [9, t('-3')],
    [10, t('')],
    [11, t('')],
    [12, t('')],
    [13, t('')],
    [14, t('')],
    [15, t('')],
    [16, t('')],
    [17, t('')],
    [18, t('')],
    [19, t('')],
]);
const allTriggerModes = [
    t(''), t(''), t(''), t(''), t(''), t(''), t(''),
];

const props = defineProps<{
    building: BlueprintBuilding,
}>();

const p = computed(() => props.building.parameters as MonitorParameters);
const tone = computed(() => allTones.get(p.value.tone)!);
const operator = computed(() => allOperators[p.value.passOperator]);
const systemWarningMode = computed(() => allTriggerModes[p.value.systemWarningMode]);
const alarmMode = computed(() => allTriggerModes[p.value.alarmMode]);
const pitch = computed(() => {
    const v = p.value.pitch - 1;
    return allPitchs[v % 12] + Math.floor(v / 12);
});
const hasLength = computed(() => !(midiMap.get(p.value.tone)!.isInstrument) || p.value.repeat);

const genDesc = computed(() => {
    switch (p.value.spawnItemOperator) {
        case SpawnItemOperator.NONE:
            return truth(false);
        case SpawnItemOperator.GENERATE:
            return t('');
        case SpawnItemOperator.CONSUME:
            return t('');
        default:
            return "??";
    }
})
</script>
